.profile {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 300px;
    margin: 30px auto;
    padding: 10px;
    box-shadow: 0 0 20px rgba(55%, 55%, 55%, 0.5);
    border-radius: 15px;
}
.description {
    width: 150px;
    text-align: center;
}
.avatar {
    width: 150px;
    border-radius: 100%;
}

.name {
    margin-bottom: 5px;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.3;
    color: blue;
}
.tag {
    margin-bottom: 5px;
    font-size: 17px;
    font-weight: 600;
    line-height: 1.3;
}
.location {
    margin-bottom: 15px;
    font-size: 17px;
    font-style: italic;
}
.stats {
    display: flex;
    align-items: center;
}
.item {
    display: flex;
    flex-direction: column;
    margin-right: 25px;
}
.label {
    margin-bottom: 5px;
    font-size: 17px;
    line-height: 1.3;
}
.quantity {
    margin-bottom: 5px;
    font-size: 17px;
    font-weight: 700;
    line-height: 1.3;
    color: blue;
}